	//@preview="preview" @navigate="navigate"
import uParse from '@/components/u-parse/u-parse.vue'
	import interfaces from '../../utils/interfaces.js';
	export default {
		components:{
			uParse
		},
		data() {
			return {
				news:{},
				contents:[]
			}
		},
		onLoad(id) {
			let ids=id.id;
			console.log("id"+ids);
			this.request({
				url: interfaces.getNewsByNid,
				data: {
				       id:ids
				    },
				success: ((res) => {
					if(res.errno==0){
						this.news=res.data;
						
					}
					console.log(res);
				
			
				})
			})
			
		},
		methods: {
			
		}
	}
